.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Loading and Error States */
.loading {
  padding: 20px;
  font-size: 1.2rem;
  color: #666;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.6; }
  50% { opacity: 1; }
}

.error {
  background-color: #fee;
  border: 1px solid #fcc;
  border-radius: 8px;
  padding: 20px;
  margin: 20px auto;
  max-width: 500px;
  color: #c00;
}

.error-details {
  font-size: 0.9rem;
  margin-top: 10px;
  opacity: 0.8;
}

.warning {
  background-color: #fff3cd;
  border: 1px solid #ffeeba;
  border-radius: 4px;
  padding: 10px 20px;
  margin: 10px auto;
  max-width: 600px;
  color: #856404;
  font-size: 0.9rem;
}

/* Game Stats */
.game-stats {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin: 20px 0;
  font-size: 1.1rem;
}

.game-stats p {
  margin: 0;
  padding: 10px 20px;
  background-color: #f0f0f0;
  border-radius: 20px;
}

/* Word Input Enhancements */
.word-input {
  margin: 20px auto;
  max-width: 400px;
}

.input-wrapper {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.word-input input {
  flex: 1;
  padding: 12px 16px;
  font-size: 1rem;
  border: 2px solid #ddd;
  border-radius: 8px;
  transition: border-color 0.3s;
}

.word-input input:focus {
  outline: none;
  border-color: #4CAF50;
}

.word-input input:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

.word-input button {
  padding: 12px 24px;
  font-size: 1rem;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.word-input button:hover:not(:disabled) {
  background-color: #45a049;
}

.word-input button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.word-input .error {
  background-color: #fee;
  border: 1px solid #fcc;
  padding: 8px 12px;
  border-radius: 4px;
  color: #c00;
  font-size: 0.9rem;
  text-align: left;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Found Words List */
.found-words {
  margin: 30px auto;
  max-width: 600px;
}

.found-words h3 {
  margin-bottom: 15px;
  color: #333;
}

.words-list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 8px;
}

.words-list li {
  background-color: #f0f0f0;
  padding: 8px 12px;
  border-radius: 16px;
  font-size: 0.9rem;
  transition: all 0.3s;
  animation: fadeIn 0.3s ease-out;
}

.words-list li:hover {
  background-color: #e0e0e0;
  transform: scale(1.05);
}

.words-list li.pangram {
  background-color: #ffd700;
  font-weight: bold;
}

.pangram-badge {
  margin-left: 4px;
  font-size: 1.2rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Tile Wheel Container */
.tile-wheel-container {
  margin: 20px auto;
}

/* Trivia Styles */
.trivia-completed {
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 8px;
  padding: 15px;
  margin: 20px auto;
  max-width: 600px;
  font-size: 1.2rem;
  color: #155724;
  animation: fadeIn 0.5s ease-out;
}

/* Trivia Clues Component */
.trivia-clues {
  margin: 30px auto;
  max-width: 600px;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #dee2e6;
}

.trivia-clues-title {
  margin-bottom: 15px;
  color: #495057;
}

.clue-progress {
  font-size: 0.9rem;
  color: #6c757d;
  font-weight: normal;
}

.clue-list {
  list-style: none;
  padding: 0;
}

.clue-item {
  padding: 10px;
  margin-bottom: 8px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.clue-item.unlocked {
  background-color: #fff;
  border: 1px solid #28a745;
  animation: slideIn 0.4s ease-out;
}

.clue-item.locked {
  background-color: #e9ecef;
  border: 1px solid #ced4da;
  opacity: 0.7;
  font-style: italic;
}

.clue-number {
  font-weight: bold;
  margin-right: 8px;
  color: #28a745;
}

.lock-icon {
  margin-right: 8px;
  font-size: 1.1rem;
}

.all-clues-unlocked {
  margin-top: 15px;
  padding: 10px;
  background-color: #fff3cd;
  border-radius: 4px;
  color: #856404;
}

/* Final Trivia Modal */
.final-trivia-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
}

.final-trivia-container {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  max-width: 500px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.final-trivia {
  text-align: center;
}

.final-trivia-title {
  margin-bottom: 20px;
  color: #343a40;
}

.trivia-question {
  font-size: 1.3rem;
  margin-bottom: 25px;
  color: #212529;
  font-weight: 500;
}

/* Hint System */
.hint-section {
  margin-bottom: 20px;
}

.hint-toggle {
  padding: 8px 16px;
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.3s;
}

.hint-toggle:hover {
  background-color: #5a6268;
}

.hints-list {
  margin-top: 15px;
  padding: 15px;
  background-color: #f8f9fa;
  border-radius: 4px;
  text-align: left;
}

.hint-title {
  font-weight: bold;
  margin-bottom: 10px;
}

.hint-item {
  margin-bottom: 5px;
  font-size: 0.9rem;
}

/* Trivia Form */
.trivia-form {
  margin-top: 20px;
}

.input-group {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.trivia-input {
  flex: 1;
  padding: 12px;
  font-size: 1rem;
  border: 2px solid #ced4da;
  border-radius: 6px;
  transition: border-color 0.3s;
}

.trivia-input:focus {
  outline: none;
  border-color: #007bff;
}

.submit-button {
  padding: 12px 24px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.submit-button:hover:not(:disabled) {
  background-color: #0056b3;
}

.submit-button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.instructions {
  color: #6c757d;
  margin: 0;
}

/* Trivia Results */
.trivia-result {
  margin-top: 20px;
}

.success-message, .error-message {
  padding: 20px;
  border-radius: 8px;
}

.success-message {
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
}

.error-message {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
}

.success-icon, .error-icon {
  font-size: 2rem;
  display: block;
  margin-bottom: 10px;
}

.result-text {
  font-size: 1.1rem;
  margin-bottom: 10px;
}

.correct-answer {
  margin: 15px 0;
}

.retry-button {
  margin-top: 15px;
  padding: 10px 20px;
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.retry-button:hover {
  background-color: #5a6268;
}

/* Close Trivia Button */
.close-trivia {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.close-trivia:hover {
  background-color: #218838;
}
